import React from 'react'
import useScrollVisibility from 'hooks/useScrollVisibility'

function PartnerLogos() {
  const [isVisible, sectionRef] = useScrollVisibility('clientes')

  const clients = [
    'Univale',
    'UCP',
    'Campo Real',
    'SESC',
    'Cresol',
    'Ictus',
    'Bacana Motors',
    'OL Construtora',
    'Empório Maziero',
    'LB Store',
    'Vila Real Odontologia',
    'Nadi Modas',
    'Restaurante Bilk',
    'Dani Kusminski',
    'Mecânica Ivaicar',
    'Líder Cred',
    'Fani Semijoias',
    'Macias & Cruz',
    'Freitas & Goedert',
    'Formigão Tintas',
    `Tec's Tintas`,
    'LB Store',
  ]

  return (
    <section
      ref={sectionRef}
      id='clientes'
      className='flex py-8 min-h-[50vh] flex-col items-center justify-center bg-purple text-white'
    >
      <div className='flex w-11/12 flex-col items-center justify-center gap-6 md:w-9/12'>
        <header className={`text-center ${isVisible ? 'animate-scaleIn' : 'opacity-0'}`}>
          <h2 className='text-4xl font-bold uppercase md:text-5xl'>Nossos Clientes</h2>
        </header>

        <div className='flex flex-wrap items-center justify-center gap-8'>
          {clients.map((client, index) => (
            <div
              key={index}
              className={`text-center text-2xl font-medium ${isVisible ? 'animate-fadeIn' : 'opacity-0'}`}
            >
              {client}
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}

export default PartnerLogos