import React from 'react'

function ContactForm() {
  return (
    <section className="flex min-h-svh w-full items-center justify-center bg-purple text-white">
      <div className="my-8 flex w-11/12 flex-col items-center justify-center md:gap-20 md:w-10/12 md:flex-row">
        
        {/* Coluna de Contato */}
        <div className="mb-8 flex flex-col justify-center uppercase">
          <header>
            <h3 className="mb-6 text-4xl font-bold">Contato</h3>
          </header>
          <ul className="space-y-4 text-2xl">
            <li>
              <h4 className="font-semibold">Telefone:</h4>
              <a
                href="https://api.whatsapp.com/send?phone=5543999024775"
                target="_blank"
                rel="noopener noreferrer"
                className="transition-colors duration-500 hover:text-green"
              >
                (43) 99902-4775
              </a>
            </li>
            <li>
              <h4 className="font-semibold">Email:</h4>
              <a
                href="mailto:samantha@dorac.com.br"
                target="_blank"
                rel="noopener noreferrer"
                className="transition-colors duration-500 hover:text-green"
              >
                samantha@dorac.com.br
              </a>
            </li>
            <li>
              <h4 className="font-semibold">Mídias Sociais:</h4>
              <a
                href="https://linktr.ee/agenciadorac"
                target="_blank"
                rel="noopener noreferrer"
                className="transition-colors duration-500 hover:text-green"
              >
                @agenciadorac
              </a>
            </li>
          </ul>
        </div>

        {/* Coluna do Formulário */}
        <div className="flex w-full flex-1 flex-col">
          <form
            action="https://formsubmit.co/samantha@dorac.com.br"
            method="POST"
            className="flex flex-col gap-4"
          >
            <input
              type="text"
              name="fullName"
              placeholder="Nome:"
              required
              className="w-full rounded-full border-2 p-6 text-lg font-semibold text-purple focus:border-green md:text-xl"
            />

            <input
              type="email"
              name="email"
              placeholder="Email:"
              required
              className="w-full rounded-full border-2 p-6 text-lg font-semibold text-purple focus:border-green md:text-xl"
            />

            <input
              type="tel"
              name="phone"
              placeholder="Telefone:"
              required
              className="w-full rounded-full border-2 p-6 text-lg font-semibold text-purple focus:border-green md:text-xl"
            />

            <input
              type="text"
              name="company"
              placeholder="Empresa:"
              required
              className="w-full rounded-full border-2 p-6 text-lg font-semibold text-purple focus:border-green md:text-xl"
            />

            <textarea
              name="message"
              placeholder="Mensagem:"
              required
              className="w-full rounded-full border-2 p-6 text-lg font-semibold text-purple focus:border-green md:text-xl"
            ></textarea>

            {/* Campos opcionais do FormSubmit */}
            <input type="hidden" name="_next" value="https://dorac.com.br" />
            <input type="hidden" name="_subject" value="Novo contato via Formulário do Site" />
            <input type="hidden" name="_captcha" value="false" />

            <button
              type="submit"
              className="mt-2 rounded-full bg-green p-5 text-2xl font-medium uppercase text-purple transition-all duration-500 hover:opacity-80"
            >
              Enviar
            </button>
          </form>
        </div>
      </div>
    </section>
  )
}

export default ContactForm